$primary        : #181818;
$secondary      : #222222;
$text           : #1C1C1C;
$border         : #696969;

@import 'cores/mixin';
@import 'cores/fonts';

body {
  @include font-400(); }

.preview {
  min-height: 100vh;
  padding: 40px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 24px;
  background: #FAFBFD;
  position: relative;

  .card-wrap {
    min-width: 300px;
    background-color: #FFF;
    // box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15)
    border-radius: 3px;
    overflow: hidden;
    position: relative;
    z-index: 2;
    .card-header {
      padding: 0;
      margin-bottom: 0;
      background-color: transparent;
      border: 0; }
    .card-body {
      padding: 0;
      margin: 0;
      background-color: #FFF;
      overflow: hidden; }
    .card-label {
      text-align: center;
      text-transform: uppercase;
      background: $primary;
      color: #FFF;
      border: 1px solid $border;
      border-bottom: 0;
      font-size: 10px;
      line-height: 24px;
      letter-spacing: 5px;
      padding: 7px 20px;
      font-weight: 600;
      border-radius: 3px 3px 0 0; }
    .card-title {
      text-align: center;
      padding: 0;
      border: 1px solid $border;
      border-top: 0;
      padding: 20px 0;
      margin: 0;
      background: #FFF;
      img {
        max-width: 220px; } }

    .card-list {
      padding: 0;
      margin: 0;
      list-style: none;
      background-color: #FFF;
      border: 1px solid $border;
      border-top: 0;
      border-radius: 0 0 3px 3px;
      &> .card-item {
        position: relative;
        width: 100%;
        margin: 0;
        &.disabled {
          pointer-events: none;
          opacity: 0.5; }
        &> a {
          display: block;
          color: $text;
          padding: 12px 26px;
          width: 100%;
          background-color: transparent;
          transition: all .5s;
          position: relative;
          &:before {
            content: "";
            display: block;
            width: 0;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: #007AFF;
            z-index: 1;
            opacity: 0;
            transition: all .3s; }
          span {
            position: relative;
            z-index: 2; }
          i {
            float: right;
            line-height: 24px;
            transform: rotate(-90deg);
            transition: transform .3s; } }
        &:hover > a {
          color: $primary;
          text-decoration: none;
          position: relative;
          &:before {
            width: 100%;
            opacity: 0.1; }
          i {
            transform: rotate(0); } }
        &.has-child > a:after {
          content: "";
          display: block;
          position: absolute;
          width: 20px;
          height: 20px;
          top: calc(50% - 10px);
          right: 26px;
          background: url(../images/ic-dropdown.svg) no-repeat center;
          background-size: contain;
          transform: none;
          transition: all .3s ease; }
        &.open > a:after {
          transform: rotate(180deg); }
        &.open > .card-sub-list {
          padding: 10px 0 10px;
          border-bottom: 1px solid #e2e2e2;
          // &:before
          //   display: block
          &> .card-item {
            max-height: 99px;
            padding: 0 26px;
            opacity: 1;
            visibility: visible; } } }

      .card-sub-list {
        padding: 0;
        margin: 0;
        list-style: none;
        background-color: lighten($primary, 5%);
        transition: all .3s;
        position: relative;
        // &:before
        //   content: ""
        //   display: none
        //   width: calc(100% - 52px)
        //   position: absolute
        //   top: 0
        //   left: 26px
        //   border-top: 1px solid $border
        &> .card-item {
          width: 100%;
          padding: 0 10px;
          max-height: 0;
          padding: 0;
          opacity: 0;
          visibility: hidden;
          transition: max-height .5s, opacity .5s, padding .5s;
          &> a {
            padding: 5px 10px;
            display: block;
            color: $text;
            width: 100%;
            border: 1px solid transparent;
            background-color: transparent;
            border: 0;
            transition: all .5s; }
          &:hover {
            &> a {
              background-color: $primary;
              text-decoration: none; } } } } } } }


